n: 0 auto;\n  .content {\n    padding: 0px, 20px;\n    margin-top: 24px;\n    .content-title {\n      text-align: left;\n      font-size: 36px;\n      font-weight: 700;\n      margin-bottom: 32px;\n      color: $color_1;\n    }\n    /*\n    No Resto Favorite\n    */\n    .resto-item-not-found {\n      display: flex;\n      padding: 32px;\n      justify-content: center;\n      align-items: center;\n      .not-found-message {\n        color: $color_1;\n        padding: 24px;\n        font-size: 20px;\n        text-align: center;\n      }\n    }\n  }\n}\n\n/*\nRestaurant\n*/\n.restaurants {\n  font-family: $font-family;\n  display: grid;\n  gap: 16px;\n  text-align: left;\n}\n.restaurant-item {\n  margin: 12px 0;\n  width: 100%;\n  overflow: hidden;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  border-radius: 5px;\n  .restaurant-item-header {\n    position: relative;\n  }\n  .restaurant-item-image {\n    object-fit: cover;\n    object-position: center;\n    width: 100%;\n    height: 250px;\n  }\n  .restaurant-item-rating {\n    font-weight: 700;\n    font-size: 14px;\n    align-items: center;\n    padding: 8px;\n    position: absolute;\n    top: 20px;\n    left: 0;\n    display: inline-block;\n    background-color: $background-color_3;\n    color: white;\n    border-top-right-radius: 5px;\n    border-bottom-right-radius: 5px;\n    .fa-star {\n      color: gold;\n      margin-right: 8px;\n    }\n  }\n  .restaurant-item-content {\n    padding: 16px 16px;\n    .restaurant-item-name {\n      margin-bottom: 4px;\n      a {\n        color: $color_1;\n        text-decoration: none;\n        font-weight: 700;\n        font-size: 18px;\n        min-width: 44px;\n        min-height: 44px;\n        padding: 12px;\n      }\n    }\n    .restaurant-item-location {\n      display: flex;\n      align-items: center;\n      padding: 0px 12px;\n      color: $color_1;\n      font-weight: 400;\n      font-size: 14px;\n    }\n  }\n}\n\n/*\n\tDetail Page\t\n*/\n.container {\n  font-family: $font-family;\n  color: $color_1;\n  margin-top: 64px;\n  margin-bottom: 32px;\n  padding: 12px 32px;\n}\n\n.content-item {\n  margin-top: 32px;\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  .resto-poster {\n    width: 100%;\n    height: 400px;\n    object-fit: cover;\n    object-position: center;\n    padding: 16px;\n  }\n  .resto-header {\n    padding: 16px;\n    .resto-name {\n      font-size: 32px;\n      margin-bottom: 12px;\n    }\n    .resto-city {\n      font-weight: 700;\n      font-size: 18px;\n      margin-bottom: 4px;\n    }\n    .resto-address {\n      font-size: 14px;\n      font-weight: 500;\n      margin-bottom: 32px;\n    }\n    .like-rating {\n      display: flex;\n      align-items: center;\n      .resto-rating {\n        border-right: 1px solid #473c33;\n        padding: 4px 12px;\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        font-size: 22px;\n        .fa-star {\n          color: gold;\n          margin-right: 8px;\n        }\n        p {\n          font-weight: bold;\n        }\n      }\n      .like-container {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        padding: 4px 12px;\n        font-size: 22px;\n        background-color: white;\n        .fa-heart {\n          color: red;\n        }\n        .like {\n          width: 55px;\n          height: 55px;\n          display: flex;\n          background-color: white;\n          align-items: center;\n          justify-content: center;\n          font-size: 24px;\n          border: 0;\n          cursor: pointer;\n        }\n      }\n    }\n  }\n  .resto-categories {\n    font-size: 20px;\n    font-weight: 700;\n    padding-bottom: 4px;\n  }\n  .categories {\n    display: flex;\n    .categories-item {\n      padding-right: 4px;\n      padding-bottom: 12px;\n      font-size: 16px;\n      font-weight: 500;\n    }\n  }\n}\n\n.resto-info {\n  text-align: left;\n  .resto-description {\n    font-size: 14px;\n    padding: 16px;\n    h3 {\n      font-size: 20px;\n      margin-bottom: 12px;\n    }\n  }\n  .resto-menu {\n    padding: 16px;\n    h3 {\n      font-size: 20px;\n      padding-bottom: 12px;\n    }\n    .menu {\n      display: grid;\n      grid-template-columns: repeat(2, 1fr);\n      gap: 16px;\n      .foods {\n        padding: 16px;\n        h4 {\n          font-size: 16px;\n          padding-bottom: 8px;\n        }\n        .food-menu {\n          font-size: 14px;\n          .food-item {\n            font-size: 14px;\n            padding-bottom: 4px;\n          }\n        }\n      }\n      .drinks {\n        padding: 16px;\n        h4 {\n          font-size: 16px;\n          padding-bottom: 8px;\n        }\n        .drink-menu {\n          font-size: 14px;\n          .drink-item {\n            font-size: 14px;\n            padding-bottom: 4px;\n          }\n        }\n      }\n    }\n  }\n  .resto-review {\n    padding: 16px;\n    h3 {\n      font-size: 20px;\n      margin-bottom: 12px;\n    }\n    .review-container {\n      display: grid;\n      gap: 16px;\n      padding: 32px;\n      .review-item {\n        color: $color_1;\n        padding: 24px;\n        background-color: $color_2;\n        border-radius: 10px;\n        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n        .review-item-header {\n          display: flex;\n          align-items: center;\n          i {\n            font-size: 20px;\n            margin-right: 16px;\n            height: 35px;\n            width: 35px;\n            padding: 12px;\n            border-radius: 100%;\n            background-color: $background-color_1;\n            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\n            display: flex;\n            align-items: center;\n            justify-content: center;\n          }\n          p {\n            font-size: 20px;\n            font-weight: 700;\n          }\n        }\n        .review-item-body {\n          padding: 12px;\n          font-style: italic;\n        }\n        .review-item-date {\n          font-size: 12px;\n        }\n      }\n    }\n    .add-review-container {\n      width: 100%;\n      padding: 32px;\n      font-family: $font-family;\n      background-color: $color_2;\n      h3 {\n        margin-bottom: 18px;\n        font-size: 20px;\n      }\n      .input-name,\n      .input-review {\n        display: flex;\n        flex-direction: column;\n        width: 100%;\n        margin-bottom: 24px;\n      }\n      .label-input-name,\n      .label-input-review {\n        font-size: 16px;\n        margin-bottom: 8px;\n        font-weight: 700;\n      }\n      input {\n        padding: 20px;\n        border-radius: 5px;\n        border: 0;\n      }\n      textarea {\n        height: 100px;\n        padding: 20px;\n        border-radius: 5px;\n        border: 0;\n      }\n      .submit-review {\n        font-size: 16px;\n        font-style: 700;\n        border: 0px;\n        border-radius: 5px;\n        padding: 16px 24px;\n        background-color: $color_4;\n        cursor: pointer;\n      }\n    }\n  }\n}\n\n/*\nError Page\n*/\n\n.error-page {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 32px;\n  margin-top: 32px;\n  .error-content {\n    color: $color_1;\n    margin: 0 auto;\n    padding: 12px;\n    text-align: center;\n    font-size: 20px;\n    .img-error {\n      width: 300px;\n      height: 100%;\n    }\n    .error-title {\n      font-weight: bold;\n      font-size: 32px;\n    }\n    .error-message {\n      margin-bottom: 32px;\n    }\n    .back-home {\n      border: 0;\n      border-radius: 5px;\n      padding: 16px 32px;\n      background-color: $color_2;\n      a {\n        color: $color_1;\n        font-weight: bold;\n        font-size: 16px;\n        text-decoration: none;\n      }\n    }\n  }\n}\n\n/*\nFooter\n*/\nfooter-element {\n  width: 100%;\n  display: flex;\n  justify-items: center;\n  align-items: center;\n  background-color: $background-color_3;\n  color: $color_3;\n  padding: 24px;\n  font-size: 14px;\n}\n\n.skeleton {\n  background: $color_3;\n  background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);\n  animation: shine 1s linear infinite;\n  border-top-right-radius: 5px;\n  border-top-left-radius: 5px;\n}\n\n@keyframes shine {\n  0% {\n    transform: translate(50% 0);\n  }\n  100% {\n    transform: translate(-150% 0);\n  }\n}\n.skeleton-text {\n  width: 75%;\n  height: 1rem;\n  border-radius: 5px;\n}\n.skeleton-location {\n  width: 50%;\n  height: 0.8rem;\n  border-radius: 5px;\n}\n\n@media screen and (min-width: 576px) {\n  .restaurant-item-name {\n    font-size: 18px;\n  }\n  .restaurant-item-desc {\n    font-size: 14px;\n  }\n}\n@media screen and (min-width: 600px) {\n  .hero {\n    background-image: url(\"/dist/heros/hero-image-small.jpg\");\n    background-size: cover;\n    background-position: center;\n  }\n  .restaurants {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n    grid-column-gap: 12px;\n    grid-row-gap: 16px;\n  }\n  .review-container {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n  }\n}\n@media screen and (max-width: 767px) {\n  .nav {\n    z-index: 100;\n    background-color: $background-color_1;\n    width: 100%;\n    top: 64px;\n    position: absolute;\n    // -webkit-transform: translate3d(0, 0, -150%);\n    // -moz-transform: translate3d(0, 0, -150%);\n    // -o-transform: translate3d(0, 0, -150%);\n    transform: translate(-150%, 0);\n    transition: transform 0.3s ease-in-out;\n    .nav-list {\n      .nav-item {\n        display: block;\n        width: 100%;\n        text-align: center;\n        a {\n          color: $color_1;\n        }\n      }\n    }\n  }\n\n  .open {\n    -webkit-transform: translate(0, 0);\n    transform: translate(0, 0);\n  }\n  .content-item {\n    display: flex;\n    flex-direction: column;\n    .resto-info {\n      margin-left: 0px;\n    }\n  }\n  .resto-info {\n    .resto-menu {\n      .menu {\n        display: grid;\n        grid-template-columns: 1fr;\n        grid-row-gap: 16px;\n      }\n    }\n  }\n}\n@media screen and (min-width: 768px) {\n  .nav-menu {\n    display: none;\n  }\n}\n@media screen and (min-width: 820px) {\n  .restaurants {\n    display: grid;\n    grid-template-columns: repeat(3, 1fr);\n  }\n  .review-container {\n    display: grid;\n    grid-template-columns: repeat(3, 1fr);\n  }\n}\n@media screen and (max-width: 1200px) {\n  hero-element {\n    width: 100%;\n  }\n}\n@media screen and (min-width: 1200px) {\n  .restaurants {\n    display: grid;\n    grid-template-columns: repeat(4, 1fr);\n  }\n  .review-container {\n    display: grid;\n    grid-template-columns: repeat(4, 1fr);\n  }\n  hero-element {\n    min-width: 1000px;\n  }\n  main {\n    max-width: 1200px;\n  }\n}\n"],"sourceRoot":""}]);
